<template>
  <div class="month">
    <div class="month-language">
      <p>Language :</p>
      <select v-model="dataRowItemOption.lang" @change="setDataRowItemOption" class="ds-form-select data-row-options">
        <option value="korean">Korean</option>
        <option value="english">English</option>
      </select>
    </div>
    <div class="month-type">
      <p>Type :</p>
      <select v-model="dataRowItemOption.type" @change="setDataRowItemOption" class="ds-form-select data-row-options">
        <option value="long">Long</option>
        <option value="short">Short</option>
      </select>
    </div>
  </div>
</template>
<script>
import { defineComponent, ref, computed } from 'vue';
import { useStore } from 'vuex';

export default defineComponent({
  props: {
    index: Number,
    dataRow: Object,
  },
  setup(props) {
    const store = useStore();
    // default value
    let dataRowItemOption = props.dataRow
      ? computed(() => props.dataRow.item.itemOption)
      : ref({
          lang: 'korean',
          type: 'short',
        });

    store.commit('setDataRowItemOption', {
      index: props.index,
      dataRowItemOption: dataRowItemOption.value,
    });

    // 선택한 data type을 store에 저장
    const setDataRowItemOption = () => {
      console.log(dataRowItemOption.value);
      store.commit('setDataRowItemOption', {
        index: props.index,
        dataRowItemOption: dataRowItemOption.value,
      });
      console.log('dataRowItemOption -->', store.getters.getDataRowItemOption(props.index));
    };

    return {
      dataRowItemOption,
      setDataRowItemOption,
    };
  },
});
</script>

